export default{
	name:'navs',
	data(){
		return{
			items:['宝贝','详情','评价'],
			currentIndex:0
		}
	},
	methods:{
		itemTap(index){
			this.currentIndex = index
			if(index === 0){
				this.backTo(0)
			}else{
				this.$parent.navScroll(index)
			}
		},
		// 滚动到具体位置
		backTo(height){
			uni.pageScrollTo({
				scrollTop:height
			})
		}
	}
}
